@import url("https://fonts.googleapis.com/css2?family=Righteous&display=swap");
:root {
  --bb-nav: #1E1E1E;
  --bb-pink: #FF5C90;
  --bb-pink-light: #EA45A0;
  --bb-green: #32806F;
  --bb-green-light: #00AD45;
  --bb-fluo: #64FF4F;
  --bb-blue: #3B86E9;
  --bb-dark-blue: #3F4C5E;
  --bb-salmon: #FF8A8A;
  --bb-purple: #8955FC;
  --bb-purple-light: #8850FE;
  --bb-grey: #404244;
  --bb-light-grey: #B3B3B3;
  --bb-red: #ED2D45;
  --bb-white: #FFFFFF;
  --bb-black: #000000;
}

@font-face {
  font-family: "Dogica";
  src: local("Dogica"), local("Dogica-Regular"), url("../../fonts/Dogica/dogica.woff2") format("woff2"), url("../../fonts/Dogica/dogica.woff") format("woff"), url("../../fonts/Dogica/dogica.otf") format("opentype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  unicode-range: U+0000-00FF, U+0100-017F;
}
@font-face {
  font-family: "Dogica";
  src: local("Dogica Bold"), local("Dogica-Bold"), url("../../fonts/Dogica/dogicabold.woff2") format("woff2"), url("../../fonts/Dogica/dogicabold.woff") format("woff"), url("../../fonts/Dogica/dogicabold.otf") format("opentype");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  unicode-range: U+0000-00FF, U+0100-017F;
}
@font-face {
  font-family: "BoldPixels";
  src: local("BoldPixels"), local("BoldPixels-Regular"), url("../../fonts/BoldPixels/BoldPixels.woff2") format("woff2"), url("../../fonts/BoldPixels/BoldPixels.woff") format("woff"), url("../../fonts/BoldPixels/BoldPixels.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  unicode-range: U+0000-00FF, U+0100-017F;
}
@font-face {
  font-family: "GNUUnifont";
  src: local("GNU Unifont"), url("../../fonts/UniFont/gnu-unifont.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  unicode-range: U+0000-00FF, U+0100-017F, U+0180-024F;
}
:root {
  --font-first: "BoldPixels", "GNUUnifont", "Dogica", monospace;
  --font-second: "Dogica", "GNUUnifont", monospace;
  --font-third: "GNUUnifont", "Dogica", monospace;
  --font-fourth: "Righteous", sans-serif;
}

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: var(--font-first);
  height: 100%;
  scroll-behavior: smooth;
  -webkit-font-smoothing: none;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeSpeed;
}

body {
  position: relative;
  background: var(--bb-nav);
  height: 100%;
}

body.menuActive {
  height: 100%;
  overflow: hidden;
}

nav {
  position: fixed;
  width: 100%;
  height: 100px;
  z-index: 10;
}

header,
main,
footer {
  overflow: hidden;
}

button {
  background: transparent;
  border: none;
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style: none;
}

.wrapper {
  margin: 0 auto;
  width: 100%;
  max-width: 1920px;
  height: 100%;
}

.nav-wrapper {
  margin: 0 auto;
  width: 100%;
  max-width: 1430px;
  height: 100%;
}

section {
  position: relative;
  display: flex;
  justify-content: center;
  min-height: 100vh;
  background: url(../img/backgroundBB.webp);
  background-size: cover;
  background-position: center;
}

.nav-menu {
  height: 100%;
  width: 100%;
}
.nav-menu.background {
  background: rgba(69, 72, 69, 0.9215686275);
}
.nav-menu__container {
  padding: 0 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.nav-menu a,
.nav-menu button,
.nav-menu li {
  cursor: pointer;
}
.nav-menu__mobile {
  display: none;
}
.nav-menu__logo, .nav-menu__others {
  width: 15%;
}
.nav-menu__logo {
  display: flex;
  justify-content: flex-end;
}
.nav-menu__logo img {
  max-width: 100px;
  width: 100%;
  height: auto;
}
.nav-menu__links {
  padding: 0.5rem 1rem;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 2rem;
  width: 70%;
  height: 100%;
  text-transform: uppercase;
  font-size: 1.4rem;
  color: var(--bb-white);
}
.nav-menu__link {
  background: var(--bb-grey);
  transition: background 0.4s;
}
.nav-menu__link:hover:not(.not-active) {
  background: var(--bb-green);
}
.nav-menu__link, .nav-menu__others-link {
  border-radius: 1.3rem;
}
.nav-menu__link a, .nav-menu__others-link a {
  padding: 1rem 2rem;
  display: block;
}
.nav-menu__others {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.nav-menu__others-link {
  background: var(--bb-grey);
  color: var(--bb-pink);
  font-size: 2rem;
  font-weight: 600;
  transition: background 0.4s;
}
.nav-menu__others-link:hover {
  background: var(--bb-purple);
  color: var(--bb-white);
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  background: url(../img/backgroundBB.webp);
  background-size: cover;
  background-position: center;
  z-index: 0;
}
.header .video__box {
  margin: 12rem 0 0;
  padding: 0 5rem;
  max-height: 880px;
  max-width: 1430px;
}
.header .video__box video {
  object-fit: cover;
  border-radius: 3rem;
  aspect-ratio: 16/9;
  box-shadow: 0px 0px 10px 2px #a8a8a8;
}
.header__socials {
  padding: 3rem 5rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 5rem;
  max-width: 1430px;
  width: 100%;
}
.header__socials h1 {
  padding: 0.5rem 2.6rem;
  font-size: 2.6rem;
  text-transform: uppercase;
  background-color: var(--bb-grey);
  color: var(--bb-pink);
  border-radius: 1.3rem;
}
.header__socials-list {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5rem;
}
.header__socials-list a {
  font-size: 2.6rem;
  text-transform: uppercase;
  color: var(--bb-black);
  transition: color 0.4s;
  cursor: pointer;
}
.header__socials-list a:hover {
  color: var(--bb-white);
}

.email,
.newsletter-delete {
  width: 100%;
  background: url(../../dist/img/emailBg.png);
  background-size: cover;
  background-position: center;
  z-index: 0;
}
.email__graphics,
.newsletter-delete__graphics {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.email__graphics--bar-code,
.newsletter-delete__graphics--bar-code {
  background: url(../../dist/img/barCode.svg);
  background-size: 180px;
  background-position: center bottom 5px;
  background-repeat: no-repeat;
  opacity: 0.7;
}
.email__graphics--eye,
.newsletter-delete__graphics--eye {
  background: url(../../dist/img/eyeEmail.svg);
  background-size: 180px;
  background-position: right 100px bottom 120px;
  background-repeat: no-repeat;
}
.email__box,
.newsletter-delete__box {
  margin: 2rem auto 0;
  padding: 4rem 0;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 640px;
  height: 100%;
  z-index: 10;
}
.email__box h3,
.newsletter-delete__box h3 {
  padding: 1rem 0 0.5rem;
  font-size: 2rem;
  text-transform: uppercase;
  color: var(--bb-white);
}
.email__box form,
.newsletter-delete__box form {
  padding: 0rem 3rem;
  display: grid;
}
.email__box form input[type=text],
.newsletter-delete__box form input[type=text] {
  margin: 1rem 0;
  padding: 2rem;
  height: 50px;
  border-radius: 1.2rem;
  line-height: 0.4rem;
  border: none;
}
.email__box form input,
.email__box form input::placeholder,
.newsletter-delete__box form input,
.newsletter-delete__box form input::placeholder {
  font-family: var(--font-third);
  font-size: 2rem;
  letter-spacing: 0.25rem;
  text-indent: 0.1rem;
  color: var(--bb-pink);
}
.email__box form input::placeholder,
.email__box form input::placeholder::placeholder,
.newsletter-delete__box form input::placeholder,
.newsletter-delete__box form input::placeholder::placeholder {
  text-transform: uppercase;
}
.email__poster,
.newsletter-delete__poster {
  z-index: 0;
}
.email__poster img,
.newsletter-delete__poster img {
  width: 100%;
  height: auto;
  max-width: 548px;
}
.email__title,
.newsletter-delete__title {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  z-index: 10;
}
.email__title h2,
.newsletter-delete__title h2 {
  display: flex;
  align-items: center;
  font-size: 4.2rem;
  text-transform: uppercase;
  color: var(--bb-white);
}
.email__title h2 img,
.newsletter-delete__title h2 img {
  padding: 0 2rem;
  width: 100%;
  height: auto;
  max-width: 180px;
}
.email__form-box,
.newsletter-delete__form-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  max-height: 400px;
  z-index: 0;
}
.email__form,
.newsletter-delete__form {
  position: relative;
}
.email__form-row,
.newsletter-delete__form-row {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: minmax(auto, 35px);
  color: var(--bb-white);
}
.email__form-row label,
.newsletter-delete__form-row label {
  margin: auto 0;
  padding: 0 1rem;
  font-size: 2.6rem;
  text-transform: uppercase;
}
.email__agree-box,
.newsletter-delete__agree-box {
  position: relative;
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
}
.email__agree-box a,
.newsletter-delete__agree-box a {
  padding: 1rem 0.5rem;
  color: var(--bb-pink);
  text-transform: uppercase;
}
.email__agree-box label,
.newsletter-delete__agree-box label {
  font-size: 1.6rem;
  color: var(--bb-black);
}
.email__agree-box input[type=checkbox],
.newsletter-delete__agree-box input[type=checkbox] {
  position: absolute;
  opacity: 0;
  height: 25px;
  width: 22px;
  cursor: pointer;
}
.email__agree-box span,
.newsletter-delete__agree-box span {
  margin-right: 1rem;
  display: inline-block;
  background-color: white;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  max-width: 20px;
  max-height: 20px;
  border-radius: 0.2rem;
}
.email__agree-box span:has(> input:checked),
.newsletter-delete__agree-box span:has(> input:checked) {
  background-color: var(--bb-purple);
}
.email__submit,
.newsletter-delete__submit {
  padding: 2rem 5rem;
  background: url(../../dist/img/btnSendBB.svg);
  background-size: cover;
  height: 70px;
  width: 150px;
  transform: scale(1);
  transition: transform 0.4s;
}
.email__submit:hover,
.newsletter-delete__submit:hover {
  transform: scale(1.1);
}
.email__send-box,
.newsletter-delete__send-box {
  margin: 0 auto;
  position: relative;
}
.email .sendingProceed,
.newsletter-delete .sendingProceed {
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  color: var(--bb-red);
  background: hsla(0, 0%, 0%, 0.8);
  border-radius: 1rem;
}
.email .sendingProceed.active,
.newsletter-delete .sendingProceed.active {
  display: flex;
}
.email__unsubscribe,
.newsletter-delete__unsubscribe {
  padding: 1rem 1rem 2rem;
  color: var(--bb-salmon);
}
.email__unsubscribe a,
.newsletter-delete__unsubscribe a {
  font-size: 1.6rem;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
}
.email.active,
.newsletter-delete.active {
  opacity: 1;
  pointer-events: all;
}

.newsletter-delete__form-box {
  max-height: 260px;
}
.newsletter-delete__title h2 {
  display: block;
  padding: 0 2rem;
  font-size: 3rem;
  text-align: center;
}

.form-popup {
  padding: 0 1rem;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 1rem;
  display: none;
  z-index: 20;
}
.form-popup.active {
  display: block;
}
.form-popup.active .form-popup__container--closeBtn {
  display: inline-block;
}
.form-popup__container {
  padding: 0 0.5rem;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.8);
  font-family: var(--font-third);
}
.form-popup__container--msg {
  padding: 1rem 0;
  font-size: 1.8rem;
  letter-spacing: 0.3rem;
  text-align: center;
  color: var(--bb-white);
}
.form-popup__container--closeBtn {
  display: none;
  margin: 2rem 0;
  padding: 1rem 2rem;
  background: var(--bb-green);
  border: none;
  color: var(--bb-white);
  font-size: 2rem;
  border-radius: 1rem;
  text-transform: uppercase;
  font-family: var(--font-third);
  letter-spacing: 0.3rem;
}

#contact {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: translate(-100%, 0%);
  transition: transform 0.4s;
}
#contact.active {
  transform: translate(0%, 0%);
  overflow-y: scroll;
}

.contact {
  position: relative;
  top: 150px;
  width: 100%;
  z-index: 0;
}
.contact .close-contact {
  padding: 0.7rem;
  position: absolute;
  top: 0;
  right: 10%;
  width: 60px;
  background: var(--bb-white);
  border-radius: 1.2rem;
  transition: background 0.4s;
  z-index: 10;
}
.contact .close-contact img {
  width: 100%;
  height: auto;
  transform: scale(1);
  transition: transform 0.4s;
}
.contact .close-contact:hover {
  background: var(--bb-pink);
}
.contact .close-contact:hover img {
  transform: scale(0.9);
}
.contact p,
.contact span {
  font-family: var(--font-first);
}
.contact__box {
  margin: 0 auto;
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 890px;
  color: #281717;
  border-radius: 0.8rem;
}
.contact__box img {
  padding-bottom: 10rem;
  max-width: 160px;
}
.contact__title-text {
  margin: 0 auto;
  max-width: 680px;
}
.contact__title-text p {
  text-align: center;
  font-size: 2.6rem;
  letter-spacing: 0.4rem;
}
.contact__adress-list {
  padding: 4rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  align-content: center;
}
.contact__adress-list span {
  padding: 2rem 0;
  font-size: 2.6rem;
  color: var(--bb-grey);
  text-transform: uppercase;
}
.contact__adress-list span a {
  text-transform: lowercase;
  transition: color 0.4s;
  letter-spacing: 0.2rem;
}
.contact__adress-list span a:hover {
  color: var(--bb-white);
}
.contact.active {
  opacity: 1;
  pointer-events: all;
}

.privacyPolicy {
  margin: 10rem auto 5rem;
  padding: 2rem 3rem;
  position: relative;
  list-style-position: inside;
}
.privacyPolicy li {
  list-style: inherit;
}
.privacyPolicy h2 {
  padding: 1rem 0 2rem;
  font-size: 3.6rem;
  text-align: center;
  text-transform: uppercase;
}
.privacyPolicy ol {
  max-width: 960px;
}
.privacyPolicy li {
  padding: 1rem 0;
  font-size: 2.6rem;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
}
.privacyPolicy ul,
.privacyPolicy ul li {
  padding: 0.2rem 0;
  font-size: 1.8rem;
  letter-spacing: 0.1rem;
  text-transform: none;
}

.footer {
  background: var(--bb-white);
  width: 100%;
  height: 100%;
}
.footer__container {
  margin: 0 auto;
  padding: 5rem 1rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
  max-width: 1430px;
}
.footer__container a {
  width: 25%;
  font-family: var(--font-third);
  font-size: 1.7rem;
  letter-spacing: 0.2rem;
  text-align: center;
}
.footer__logos {
  margin: 0 1rem;
  padding: 2rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  max-width: 620px;
  width: 50%;
  background: var(--bb-black);
  border-radius: 1.6rem;
}
.footer__logos img {
  width: 100%;
  height: auto;
  max-height: 45px;
}

.nav-menu .not-active a {
  cursor: default;
  filter: opacity(0.5);
}

@media screen and (max-width: 1200px) {
  .header__socials {
    display: grid;
    grid-template-rows: max-content;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }
  .header__socials-list {
    grid-row: 1;
    grid-column: span 2;
  }
  .footer__graphics--bar-code {
    background-position: center bottom 10px;
  }
  .footer__container {
    padding: 0 1rem 2rem;
    display: grid;
    justify-items: center;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 100px;
    gap: 2rem;
  }
  .footer__container a {
    width: 100%;
  }
  .footer__logos {
    grid-row: 2;
    width: 100%;
  }
}
@media screen and (max-width: 1100px) {
  .header .video__box {
    padding: 0 2rem;
  }
  .header__socials h1 {
    font-size: 2.2rem;
  }
  .header__socials-list {
    gap: 2rem;
  }
  .header__socials-list a {
    font-size: 2.2rem;
  }
}
@media screen and (max-width: 992px) {
  .nav-menu {
    justify-content: flex-end;
  }
  .nav-menu__container {
    padding: 0 2rem;
    justify-content: space-between;
  }
  .nav-menu__mobile {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .nav-menu__mobile button {
    color: var(--bb-white);
    background-color: transparent;
    border: none;
    width: 40px;
    cursor: pointer;
  }
  .nav-menu__logo, .nav-menu__mobile, .nav-menu__others {
    width: 100%;
  }
  .nav-menu__links {
    padding: 5rem 0 0;
    position: absolute;
    top: 100px;
    left: -100%;
    flex-direction: column;
    justify-content: start;
    gap: 5rem;
    width: 100%;
    min-height: 100vh;
    height: 100%;
    background: var(--bb-light-grey);
    overflow-y: scroll;
    transition: transform 0.4s;
  }
  .nav-menu__links.active {
    transform: translateX(100%);
  }
  .contact {
    top: 120px;
  }
  .footer__container {
    padding: 4rem 3rem 0;
  }
}
@media screen and (max-width: 768px) {
  .header__socials {
    padding: 3rem 2rem;
    flex-direction: column;
    gap: 3rem;
  }
  .email__graphics--eye,
  .newsletter-delete__graphics--eye {
    display: none;
  }
  .email__graphics--bar-code,
  .newsletter-delete__graphics--bar-code {
    background-size: 120px;
  }
  .email__title,
  .newsletter-delete__title {
    padding: 6rem 0 0;
  }
  .email__title h2,
  .newsletter-delete__title h2 {
    font-size: 3.8rem;
  }
  .email__form,
  .newsletter-delete__form {
    justify-content: center;
  }
  .email__form h3,
  .newsletter-delete__form h3 {
    font-size: 1.2rem;
  }
  .email__form form,
  .newsletter-delete__form form {
    grid-template-rows: repeat(3, auto);
  }
  .email__form form input[type=text],
  .newsletter-delete__form form input[type=text] {
    height: 40px;
  }
  .email__form form input[type=text]::placeholder,
  .newsletter-delete__form form input[type=text]::placeholder {
    font-size: 1.2rem;
  }
  .email__form-row label,
  .newsletter-delete__form-row label {
    font-size: 2rem;
  }
  .form-popup__container--msg {
    font-size: 1.4rem;
    letter-spacing: 0.35rem;
  }
  .contact__box {
    padding: 2rem;
  }
  .contact__box img {
    padding-bottom: 5rem;
    max-width: 125px;
  }
  .contact__title-text p {
    font-size: 2rem;
  }
  .contact__adress-list span {
    padding: 1rem 0;
    font-size: 2rem;
    text-align: center;
  }
  .contact__adress-list span a {
    padding: 0.5rem 0;
    display: block;
  }
}
@media screen and (max-width: 565px) {
  nav {
    height: 60px;
  }
  .nav-menu__container {
    padding: 0.2rem 0 0;
  }
  .nav-menu__logo, .nav-menu__mobile, .nav-menu__others {
    justify-content: center;
  }
  .nav-menu__mobile button {
    width: 30px;
  }
  .nav-menu__logo img {
    max-height: 45px;
    width: auto;
  }
  .nav-menu__others-link a {
    padding: 0.5rem 1rem;
    font-size: 1.6rem;
  }
  .nav-menu__links {
    top: 60px;
  }
  .header__socials {
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }
  .header__socials-list a {
    font-size: 2rem;
  }
  .email__box form input[type=text],
  .newsletter-delete__box form input[type=text] {
    margin: 0.4rem 0;
    padding: 0.5rem;
    height: 30px;
  }
  .email__box form input,
  .email__box form input::placeholder,
  .newsletter-delete__box form input,
  .newsletter-delete__box form input::placeholder {
    padding: 0.5rem;
    font-size: 1.8rem;
  }
  .email__agree-box label,
  .newsletter-delete__agree-box label {
    font-size: 1.4rem;
  }
  .email__unsubscribe,
  .newsletter-delete__unsubscribe {
    text-align: center;
  }
  .contact {
    top: 100px;
  }
  .contact .close-contact {
    padding: 0.7rem;
    width: 35px;
    border-radius: 0.9rem;
  }
  .privacyPolicy {
    margin: 7rem auto 5rem;
  }
  .privacyPolicy h2 {
    padding: 1rem 0 2rem;
    font-size: 2.8rem;
  }
  .privacyPolicy li {
    font-size: 2rem;
  }
  .privacyPolicy ul,
  .privacyPolicy ul li {
    font-size: 1.6rem;
  }
  .footer__graphics--bar-code {
    background-size: 140px;
    background-position: center bottom 10px;
  }
  .footer__container {
    padding: 2rem 1rem 1rem;
    grid-template-rows: 1fr 1fr 70px;
  }
  .footer__container a {
    font-size: 1.5rem;
  }
  .footer__dot, .footer__bb {
    text-align: center;
  }
  .footer__dot {
    padding: 2rem 0 1rem;
  }
  .footer__bb {
    padding: 1rem 0 2rem;
  }
  .footer__logos {
    padding: 2rem 0;
    gap: 1rem 0;
  }
  .footer__logos img {
    max-height: 30px;
  }
}
@media screen and (max-width: 360px) {
  .header__socials {
    padding: 3rem 1rem;
  }
  .header__socials a {
    font-size: 1.6rem;
  }
}
@media (orientation: portrait) {
  .header .video__box {
    margin: 12rem 0 0;
    max-height: unset;
  }
  .header .video__box video {
    aspect-ratio: 3/4;
  }
}
@media screen and (max-width: 565px) and (orientation: portrait) {
  .header .video__box {
    margin: 8rem 0 0;
  }
}